import React, { Component } from 'react';
import requester from '../../../utils/requester';

export default class Details extends Component {
    constructor(props) {
        super(props);

        this.state = {
            terminDate: '',
            terminHour: '',
            street: '',
            postCode: '',
            company: '',
            warmRent: '',
            rooms: '',
            _id: ''
        }
    }

    getTerminById = () => {
        let terminId = this.props.match.params.id;
        const ENDPOINT = `termins/${terminId}`;

        return requester.get('appdata', ENDPOINT, 'kinvey');
    };

    componentDidMount = () => {
        //Do when component is rendered...
        this.getTerminById().then((res) => {
            this.setState({
                terminDate: res.terminDate,
                terminHour: res.terminHour,
                street: res.street,
                postCode: res.postCode,
                company: res.company,
                warmRent: res.warmRent,
                rooms: res.rooms,
                _id: res._id
            });
        });
    };

    render = () => {
        return (
            <main>
                <section id="viewAds" className="viewAds">
                    <h1 className="titleForm">Детайли:</h1>
                    <div id="ads" className="ads">

                        <table>
                            <thead>
                            <tr>
                                <th colSpan="3">Детайли за термин:</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td className="productDetail">Дата</td>
                                <td>
                                    {this.state.terminDate}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Час</td>
                                <td>
                                    {this.state.terminHour}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Адрес</td>
                                <td>
                                    {this.state.street}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Пощенски код</td>
                                <td>
                                    {this.state.postCode}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Фирма</td>
                                <td>
                                    {this.state.company}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Стаи</td>
                                <td>
                                    {this.state.rooms}
                                </td>
                            </tr>
                            <tr>
                                <td className="productDetail">Топъл наем</td>
                                <td>
                                    {Number(this.state.warmRent).toFixed(2)}€
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </section>
            </main>
        )
    }
}


