import React, {Component} from 'react';
import {Link} from 'react-router-dom';

export default class Termin extends Component {
    render = () => {
        let editEndpoint = `/edit/${this.props._id}`;
        let deleteEndpoint = `/delete/${this.props._id}`;
        let mapUrl = `https://www.google.com/maps/search/?api=1&query=Berlin+${this.props.street}+${this.props.postCode}`;

        return (
            <li>
                <div className="productActions"><Link to={editEndpoint}><button className="editLink">&#9998;</button></Link></div>
                <div className="productActions"><Link to={deleteEndpoint}><button className="deleteLink">&#10006;</button></Link></div>
                <Link to={"/details/" + this.props._id}>
                    <div className="productBrand">Дата: {this.props.terminDate}</div>
                    <div className="productModel">Час: {this.props.terminHour}</div>
                    <div className="productPicture"><img src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/9f/Facebook_Home_logo.svg/2000px-Facebook_Home_logo.svg.png" height="180" alt="Termin Logo" /></div>
                    <div className="productSize">{this.props.street}</div>
                    <div className="productSize">{this.props.postCode}</div>
                    <div className="productInfo">{this.props.company} | {this.props.rooms}</div>
                    <div className="productPrice">{this.props.warmRent}€</div>
                </Link>
                <div className="productSize"><a href={mapUrl} target="_blank">ВИЖ КАРТА</a></div>
            </li>
        )
    }
}


